import { Meteor } from 'meteor/meteor';
import { Session } from 'meteor/session';
import { get } from 'lodash';

import React from 'react';
import { browserHistory } from 'react-router';
import { Glass } from 'meteor/clinical:glass-ui';
import { FlatButton } from 'material-ui';
import PropTypes from 'prop-types';

export class OrganizationsFooterButtons extends React.Component {
  constructor(props) {
    super(props);
  }
  openLink(url, callback){
    console.log("openLink", url);

    if(typeof callback === "function"){
      callback();
    }

    browserHistory.push(url);
  }
  searchOrganizations(url){
    console.log('openQuestionnairePreview', url)
    browserHistory.push('/search-organizations');    
    // browserHistory.push(get(Meteor, 'settings.public.modules.links.QuestionnairePreview', '/'))
  }
  render() {
    return (
      <div>
        <FlatButton label='Search' onClick={this.searchOrganizations.bind(this) } style={ Glass.darkroom({marginLeft: '20px'}) } ></FlatButton>
      </div>
    );
  }
}

export default OrganizationsFooterButtons;