import { BaseModel } from 'meteor/clinical:base-model';
import { Mongo } from 'meteor/mongo';
import SimpleSchema from 'simpl-schema';
import { get } from 'lodash';


News = new Mongo.Collection('News');

NewsSchema = new SimpleSchema({
    "resourceType" : {
      type: String,
      defaultValue: "NewsPost"
    },
    "identifier" : {
      optional: true,
      type:  Array
    },
    "identifier.$" : {
      optional: true,
      type:  Object 
    },
    "title" : {
      optional: true,
      type: String
    },
    "date" : {
      optional: true,
      type: String
    },
    "text" : {
      optional: true,
      type: String
    },
    "index": {
        optional: true,
        type: Number
    }
});
  
News.attachSchema(NewsSchema);

export default { News, NewsSchema };