export class DownloadsPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: [],
      description: '',
      slideIndex: 0
    };    
  }
  getMeteorData() {
    let data = {
      style: {},
      slideIndex: Session.get('marketingPageTabIndex'),
      architectureSlideIndex: Session.get('architectureTabIndex'),
      pageHeight: Session.get('appHeight') + 'px',
      providedEmail: Session.get('providedEmail'),
      providedCompanyInfo: Session.get('providedCompanyInfo'),
      qualifiedOrganization: Session.get('qualifiedOrganization'),
      stats: {
        users: 0,
        services: 0,
        auditEvents: 0,
        consents: 0,
        organizations: 0,
        subscriptions: 0,
        procedureRequests: 0,
        bundles: 0
      }
    }

    data.stats = Session.get('serverStats')

    data.style.pageBackground = Glass.getContextImage();

    return data;
  }
  handleChange(value){
    Session.set('marketingPageTabIndex', value)
  };



  render(){

    return(
      <div id="DownloadsPage" style={this.data.style.pageBackground}>
        <FullPageCanvas>

          <Row id='statsSection' style={{
                                  backgroundColor: 'rgba(128, 192, 224, 0.12)', 
                                  marginTop: '100px', 
                                  textAlign: 'left', 
                                  color: '#48C1EF', 
                                  borderTop: '3px solid #48C1EF', 
                                  borderBottom: '3px solid #48C1EF' 
                                }}  >
                <Row style={{marginTop: '40px', marginBottom: '20px', marginLeft: '20px'}}>
                  <Col xsOffset={2} xs={2}>
                    <h1>{ this.data.stats.users }</h1>
                    <h4 style={{fontWeight: 200}}>Users</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ this.data.stats.auditEvents }</h1>
                    <h4 style={{fontWeight: 200}}>Audit Events</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ this.data.stats.services }</h1>
                    <h4 style={{fontWeight: 200}}>Services</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ this.data.stats.consents }</h1>
                    <h4 style={{fontWeight: 200}}>Consents</h4>
                  </Col>
                </Row>
                <Row style={{marginTop: '20px', marginBottom: '40px', marginLeft: '20px'}}>
                  <Col xsOffset={2} xs={2}>
                    <h1>{ get(this, 'data.stats.bundles', 0) }</h1>
                    <h4 style={{fontWeight: 200}}>Bundles</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ get(this, 'data.stats.organizations', 0)  }</h1>
                    <h4 style={{fontWeight: 200}}>Organizations</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ this.data.stats.subscriptions }</h1>
                    <h4 style={{fontWeight: 200}}>Subscriptions</h4>
                  </Col>
                  <Col xs={2}>
                    <h1>{ this.data.stats.subscriptions }</h1>
                    <h4 style={{fontWeight: 200}}>ProcedureRequests</h4>
                  </Col>
                </Row>

          </Row>
          
        </FullPageCanvas>
      </div>
    );
  }




}
ReactMixin(DownloadsPage.prototype, ReactMeteorData);
export default DownloadsPage;

